
.box {
    display: inline-block;
    width: 75px;
    cursor: pointer;
    border: 2px solid #eee;
    padding: 1px;
}
.box:hover {
    border: 2px solid blue;
}
.box.selected {
    border: 2px solid red;
}
div#header {
    height: 68px;
    /* top: -53px; */
    margin-top: -26px;
}
h3.subtitle {
    float: left;
    padding-left: 11px;
}
h3.subtitle2 {
    float: right;
    padding-right: 11px;
}
#title {
  width: 200px;
  margin-left: -100px;
  position: absolute;
  top: 24px;
  left: 50%;
  font-size: 23px;
}
img#logo {
  width: 200px;
  margin-left: -98px;
  position: absolute;
  top: 0px;
  left: 50%;
}
nav#lang {
  position: absolute;left: 0px;
  top: 0px;
  display: none;
}
div#sns {
  position: fixed;
    right: 0px;
    top: 65px;
    height: 20px;
    padding: 18px;
}
div#google_translate_element {
    left: 11px;
    position: absolute;
    top: -15px;
}
button {
    background-color: #102cf6;
    color: #f0f0f0;
    font-size: 18px;
    border-radius: 5px;
    margin-top: 7px;
    padding: 6px;
    padding-left: 20px;
    padding-right: 20px;
    font-weight: bolder;
}
nav#lang ul {
    list-style: none;
    padding-left: 15px;
}
div#sns a {
    display: block;
}
nav#lang li {
    display: inline-block;
}
.box {
    display: inline-block;
    width: 75px;
    vertical-align: top;
    overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}



